export default "/* stylelint-disable prettier/prettier */\n/* Aspect ratios */\n/* Transitions */\n/* Typography */\n/* Shadows */\n/* Fonts */\n@keyframes skeleton-loading-malsync {\nto {\n    transform: translateX(100%);\n}\n}\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\nhtml,\nbody {\n  margin: 0;\n  padding: 0;\n}\nhtml {\n  background-color: var(--cl-backdrop);\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-attachment: fixed;\n}\nhtml.backImage {\n  background-image: var(--cl-back-image);\n}\nhtml.backImage #minimalApp {\n  backdrop-filter: blur(5px);\n}\nbody {\n  color: var(--cl-text);\n  overflow: overlay;\n  display: inherit;\n  height: initial;\n}\nbody,\nhtml,\n.dropdown-pop {\n  scrollbar-width: none;\n}\nbody::-webkit-scrollbar,\nhtml::-webkit-scrollbar,\n.dropdown-pop::-webkit-scrollbar {\n  width: 15px;\n  background-color: transparent;\n}\nbody::-webkit-scrollbar-thumb,\nhtml::-webkit-scrollbar-thumb,\n.dropdown-pop::-webkit-scrollbar-thumb {\n  border: 6px solid transparent;\n  background-color: #c1c1c1;\n  background-clip: content-box;\n}\nbody::-webkit-scrollbar-thumb:hover,\nhtml::-webkit-scrollbar-thumb:hover,\n.dropdown-pop::-webkit-scrollbar-thumb:hover {\n  border: 4px solid transparent;\n}\n#minimalApp {\n  background-color: var(--cl-background);\n  max-width: 1400px;\n  min-height: 100vh;\n  margin-left: auto;\n  margin-right: auto;\n  display: flex;\n  flex-direction: column;\n}\nhtml.no-sidebar body {\n  background-color: var(--cl-background);\n}\nhtml.no-sidebar #minimalApp {\n  background-color: transparent;\n  backdrop-filter: none;\n}\nbutton {\n  border-radius: 10px;\n  border: none;\n  background-color: transparent;\n  padding: 0;\n  font-size: inherit;\n  color: inherit;\n}\nbutton:focus {\n  outline: 0;\n}\nbutton:focus-visible {\n  outline: 2px solid var(--cl-secondary);\n}\np {\n  margin-top: 0;\n}\n:root {\n  --cl-text: #333333;\n  --cl-light-text: #828282;\n  --cl-primary: #4c8ff2;\n  -